import React from 'react';
import '../App.css';
import qs from 'query-string';
import Axios from 'axios';
import Header from './Shared/Header';
import PriceList from './PriceList';
import Profile from './Profile';
import * as helper from '../Utils/Helper';
import * as TandS from '../Utils/TermsAndConditions';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

export default class ShopPage extends React.Component {
  constructor () {
    super();
    this.entryClickHandler = this.entryClickHandler.bind(this);

    this.state = {
      shopId: undefined,
      loadDataFailed: false,
      missingParameter: false,
      shopInfo: undefined,
      shop404: undefined,
      loading: false,
      termsAndConditions: undefined
    };
  }

  entryClickHandler (unitTypeId) {
    const shopId = qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).shopid;
    window.parent.location.href = `/entry/index.html?shopid=${shopId}&unittypeid=${unitTypeId}`;
  }

  async componentDidMount () {
    const shopId = qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).shopid;

    if (shopId === undefined || shopId.trim() === '') {
      this.setState({ missingParameter: true });
      return;
    } else {
      this.setState({ missingParameter: false });
    }

    this.setState({ loading: true });

    try {
      const termsAndConditions = await TandS.GetTermsAndConditionsUrls(shopId);
      this.setState({ termsAndConditions });

      const url = `${window.storeganise_api_url}/v1/settings`;
      const response = await Axios.get(url, { headers: { Accept: 'application/json, text/plain, */*' } });
      // const shopAdditionalInfo = await Axios.get('/store/shop-additional-info.json');
      this.setState({ loadDataFailed: false });
      // console.log(response.data);
      const shopData = response.data.sites.find(site => site.id === shopId);
      if (shopData === undefined) {
        this.setState({ shop404: true, loading: false });
      } else {
        const shopInfo = helper.GetShopInfo(shopId, shopData);
        document.title = `押入れ産業 - ${shopInfo.shopName}`;
        this.setState({ shopInfo, loading: false });
      }
    } catch (error) {
      var logMessage = { shopId };

      if (error.response) {
        logMessage = {
          ...logMessage,
          response_data: error.response.data,
          response_status: error.response.status,
          response_headers: error.response.headers
        };
      } else if (error.request) {
        logMessage = {
          ...logMessage,
          response_request: error.request
        };
      } else {
        logMessage = {
          ...logMessage,
          error_message: error.message
        };
      }
      console.log(error.config);
      this.setState({ loadDataFailed: true, loading: false });
      helper.logIt({ method: 'ShopPage.componentDidMount', logMessage });
    }
  }

  render () {
    const shopInfo = this.state.shopInfo;

    var shopImageUrl;
    var unitTypeList;
    var shopName;

    if (shopInfo !== undefined) {
      shopName = shopInfo.shopName;
      shopImageUrl = shopInfo.imageUrl;
      unitTypeList = shopInfo.unitTypeList;
    }

    var oshiireTokuteishouUrl = '';
    const termsAndConditions = this.state.termsAndConditions;
    if (termsAndConditions !== undefined) {
      oshiireTokuteishouUrl = termsAndConditions.oshiire_tokuteishou === undefined ? '' : termsAndConditions.oshiire_tokuteishou;
    }

    return (
      <>
        <Header />
        <BlockUi
          blocking={this.state.loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <article className='service'>
            <section className='contents'>
              <div className='container'>
                <div id='errors' className='row'>
                  <div className='col-md-12 validation-error'>
                    {
                      this.state.loadDataFailed === true &&
                        <div>Failed to load unit types data, please try it again later.</div>
                    }
                    {
                      this.state.missingParameter === true &&
                        <div>Missing parameter, please contact the administrator.</div>
                    }
                    {
                      this.state.shop404 === true &&
                        <div>Shop page does not exist.</div>
                    }
                  </div>
                </div>

                {
                  (this.state.loadDataFailed !== true && this.state.missingParameter !== true && this.state.shop404 !== true) &&
                    <>
                      <div className='col-sm-12'>
                        <div id='shop_banner'>
                          <div>
                            {shopName}
                          </div>
                        </div>
                      </div>
                      <div className='col-sm-6'>
                        <div style={{ paddingBottom: '5px' }}>
                          {
                            shopImageUrl !== undefined &&
                              <img src={shopImageUrl} alt='shop' style={{ width: '100%' }} />
                          }
                          {
                            shopImageUrl === undefined &&
                              <div style={{ height: '350px' }}>&nbsp;</div>
                          }
                        </div>
                      </div>
                      <div className='col-sm-6 info'>
                        <Profile
                          shopInfo={shopInfo}
                        />
                      </div>
                      <div className='col-md-12 col-xs-12'>
                        <PriceList
                          unitTypeList={unitTypeList}
                          entryClickHandler={this.entryClickHandler}
                        />
                      </div>
                      <div className='col-md-12 col-xs-12 terms-and-agreements'>
                        <a href={oshiireTokuteishouUrl} target='_blank' rel='noopener noreferrer'>特定商取引法</a>
                      </div>
                    </>
                }
              </div>
            </section>
          </article>
        </BlockUi>
      </>
    );
  }
}
