import Axios from 'axios';
import * as helper from './Helper';

export async function GetTermsAndConditionsUrls (shopId) {
  let overallSettings;
  const baseUrl = window.terms_and_conditions_base_url === undefined ? '' : window.terms_and_conditions_base_url;

  try {
    const response = await Axios({
      method: 'get',
      url: `${baseUrl}/setting.json?v=${Date.now()}`,
      headers: { 'Content-Type': 'application/json' }
    });

    overallSettings = response.data.overall;

    if (response.data[shopId] !== undefined) {
      overallSettings = { ...overallSettings, ...response.data[shopId] };
    }
  } catch (error) {
    helper.logIt({ method: 'GetTermsAndConditionsUrls', error: error.message, shopId });
  }

  return overallSettings;
}
