import React from 'react';
import uuid4 from 'uuid/v4';

export default function Profile (props) {
  const {
    shopInfo
  } = props;

  var address;
  var phone;
  var info;

  if (shopInfo !== undefined) {
    phone = shopInfo.phone;
    if (shopInfo.address !== undefined) {
      address = shopInfo.address.split('\n');
    }

    if (shopInfo.info !== undefined) {
      info = shopInfo.info.split('\n');
    }
  }

  return (
    <table className='table table-bordered'>
      <tbody>
        <tr>
          <th>住所</th>
          <td className='text-left'>
            {address !== undefined ? address.map(line => <div key={uuid4()}>{line}</div>) : ''}
          </td>
        </tr>
        <tr>
          <th>TEL</th>
          <td>{phone}</td>
        </tr>
        <tr>
          <td colSpan='2'>
            {info !== undefined ? info.map(line => <div key={uuid4()}>{line}&nbsp;</div>) : ''}
          </td>
        </tr>
      </tbody>
    </table>
  );
}
