import logger from './Logger';

export function logIt (logBody) {
  try {
    logger.push({ logBody, git_hash: window.git_hash, widget: 'store', user_agent: navigator.userAgent });
  } catch (error) {
    console.log(error);
  }
}

export function GetShopInfo (shopId, shopData) {
  const availability = shopData.availability;
  const unittypes = shopData.unitTypes;
  const canApplyCount = window.can_apply_unit_count_greater_or_equal_to;

  // console.log(window.can_apply_unit_count_greater_or_equal_to, availability, unittypes);

  const unitTypeList = [];
  const shopAdditionalInfo = window.shop_additional_info.find(item => item.shopId === shopId);
  unittypes.forEach(ut => {
    const availabilityItem = availability[ut.id];

    if (availabilityItem !== undefined) {
      if (availabilityItem.total > 0) {
        const canApply = (availabilityItem.available >= canApplyCount);

        const unitType = ut.subtitle.ja;
        const otherInfo = shopAdditionalInfo.unitTypes.find(item => item.unitType === unitType);

        var sort = 99999;
        if (otherInfo !== undefined && otherInfo.sort !== undefined) {
          sort = otherInfo.sort;
        }

        unitTypeList.push({
          canApply,
          id: ut.id,
          unitType: ut.subtitle.ja,
          size: ut.title.ja,
          price: ut.price,
          priceDisp: ut.price.toLocaleString(),
          sort: sort,
          otherInfo: otherInfo
        });
      }
    }
  });

  return {
    shopName: shopData.title.ja,
    imageUrl: shopData.image,
    address: shopData.address.ja,
    phone: shopData.phone,
    lat: shopData.lat,
    lng: shopData.lng,
    info: shopData.info.ja,
    unitTypeList
  };
}
