import React from 'react';
import { Button } from 'react-bootstrap';

export default function PriceList (props) {
  const {
    unitTypeList,
    entryClickHandler
  } = props;

  if (unitTypeList !== undefined) {
    unitTypeList.sort((item1, item2) => item1.sort - item2.sort);
  }

  return (
    <>
      <div className='price'>
        <table>
          <thead>
            <tr>
              <th className='price_type'>タイプ</th>
              <th className='price_number'>畳数</th>
              <th className='price_size'>幅×奥行×高さ(cm)</th>
              <th className='price_official'>正規料金</th>
              <th className='price_campaign'>キャンペーン料金</th>
              <th className='price_initial'>初期費用</th>
              <th className='price_remarks'>備考</th>
              <th className='price_order'>お申し込み</th>
            </tr>
          </thead>

          <tbody id='price_table'>
            {
              unitTypeList !== undefined &&
                  unitTypeList.map(ut => {
                    var dimension;
                    var initFee;
                    var note;
                    var discountPrice;
                    if (ut.otherInfo !== undefined) {
                      dimension = `${ut.otherInfo.width}×${ut.otherInfo.depth}×${ut.otherInfo.height}`;
                      initFee = ut.otherInfo.initFee.toLocaleString();
                      discountPrice = ut.otherInfo.discountPrice;
                      note = ut.otherInfo.note;
                    }

                    return (
                      <tr key={ut.id}>
                        <td className='price_type'>{ut.unitType}</td>
                        <td className='price_number'>{ut.size}</td>
                        <td className='price_size click'>{dimension}</td>
                        <td className='price_official'>{ut.priceDisp} 円</td>
                        <td className='price_campaign text-danger'><strong>{discountPrice}</strong></td>
                        <td className='price_initial click'>{initFee} 円</td>
                        <td className='price_remarks acc_txt click'>{note}</td>
                        <td className='price_order'>
                          {ut.canApply === true ? <Button onClick={entryClickHandler.bind(this, ut.id)} className='btn btn-success' style={{ width: '120px' }}>お申し込み</Button>
                            : <Button className='btn btn-secondary' style={{ width: '120px' }} disabled><span className='button-text'>ご利用中</span></Button>}
                        </td>
                      </tr>
                    );
                  })
            }
          </tbody>
        </table>
      </div>
      <p className='text-left'><small><strong>（※正規料金は税抜価格、初期費用は税込価格となっております。）</strong></small></p>
    </>
  );
}
